* {
  margin: 0;
  text-rendering: optimizeLegibility;
}

*:focus {
  outline: none;
}

html {
  width: 100%;
  font-family: 'Roboto', sans-serif;
}

body {
  -webkit-font-smoothing: antialiased;
  background-color: #f6f6f8;
  margin: 0;
  padding-top: 0;
}

@supports (display: grid) {
  #registerWrapper {
    display: grid;
    grid-template-rows: 0.5fr 5fr;
    grid-template-columns: 1fr 5fr 1fr;
    background-color: #f6f6f8;
  }

  #registerHeader {
    grid-column: 1 / span 3;
    grid-row: 1 / span 1;
    background-image: linear-gradient(to right, #151f36, #5779da);
  }

  #registerContent {
    grid-column: 2 / span 1;
    grid-row: 2 / span 1;
  }

  #registerFooter {
    grid-column: 1 /span 3;
    grid-row: 3 /span 1;
    background-color: #e5e5e5;
  }
}

#registerHeader img {
  width: 136px;
  margin-top: 18px;
  margin-bottom: 18px;
  margin-left: 32px;
}

#registerContent {
  text-align: center;
  display: flex;
  flex-direction: column;
}

#registerTitle {
  font-size: 40px;
  color: #ff720b;
  margin-top: 45px;
}

#registerSub {
  font-size: 21px;
  font-weight: 300;
  color: #162036;
  margin-top: 16px;
}

#formContainer {
  display: flex;
  justify-content: center;
  margin-top: 70px;
  margin-bottom: 70px;
}

#registerForm {
  display: flex;
  flex-direction: column;
  margin-right: 35px;
  flex: 1;
  text-align: left;
}

#registerForm input {
  border: solid 1px #d6d6d6;
  background-color: #ffffff;
  height: 22px;
  margin-bottom: 16px;
  padding: 7px 10px;
  font-size: 14px;
}

#region {
  border: solid 1px #d6d6d6;
  background-color: #ffffff;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: normal;
  font-style: italic;
  color: #666666;
  text-indent: 5px;
  -webkit-appearance: initial;
  -webkit-padding-start: 9px;
}

select {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.mandatory:before {
  content: "*";
  color: red;
}

#passwordReq {
  font-size: 14px;
  color: #666666;
}

input::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  font-size: 14px;
  font-weight: normal;
  font-style: italic;
  color: #666666;
}
input::-moz-placeholder { /* Firefox 19+ */
  font-size: 14px;
  font-weight: normal;
  font-style: italic;
  color: #666666;
}

input:-ms-input-placeholder { /* IE 10+ */
  font-size: 14px;
  font-weight: normal;
  font-style: italic;
  color: #666666;
}

input:-moz-placeholder { /* Firefox 18- */
  font-size: 14px;
  font-weight: normal;
  font-style: italic;
  color: #666666;
}

#formContainer hr {
  border: none;
  border-left: 1px solid #d6d6d6;
  width: 1px;
}


#captchaContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-left: 35px;
}

#byClicking {
  width: 50%;
  margin-top: 25px;
  display: flex;
  text-align: left;
}

#byClicking input {
  margin-right: 10px;
  margin-top: 10px;
}

#byClicking span {
}

#byClicking span a {
  text-decoration: none;
}

#registerContainer {
  display: flex;
}

#registerButton, #registerButtonTest  {
  border-radius: 5px;
  background-color: #ff720b;
  margin-top: 45px;
  height: 50px;
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0.5;
}

.activeButton {
  opacity: 1 !important;
}

#registerButton span {
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
}

#loader {
  display: none;
  align-self: flex-end;
  margin-bottom: 7px;
  margin-left: 20px;
}

#checkboxContainer {
  display: flex;
  align-items: center;
}

#checkboxContainer input {
  margin-bottom: 0;
  margin-right: 5px;
}

#checkboxContainer span {
}


.conditionButton {
  display: none;
  text-align: left;
  color: red;
  align-self: baseline;
  margin-top: 5px;
  font-style: italic;
  font-size: 15px;
}

.success {
  color: #5779da;
}

/* Media queries begin, don't panic! */

@media screen and (max-width: 1400px) {

  #byClicking {
    width: 65%;
  }
}

@media screen and (max-width: 824px) {
  #formContainer {
    flex-direction: column;
  }

  #formContainer hr {
    height: 40px;
    width: 95%;
    margin-top: 40px;
    border-top: 1px solid #d6d6d6;
    border-left: none;
  }

  #captchaContainer {
    padding-left: 0;
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 450px) {
  #registerContent {
    align-items: center;
  }

  #registerTitle {
    width: 60%;
  }

  #registerSub {
    width: 50%;
  }

  #registerForm {
    margin-right: 0;
  }

  #captchaContainer {
    align-items: center;
  }

  #byClicking {
    width: 70%;
  }

}


























* {
  margin: 0;
  text-rendering: optimizeLegibility;
}

*:focus {
  outline: none;
}

html {
  width: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
  background-color: #f6f6f8;
  margin: 0;
  padding-top: 0;
}

@supports (display: grid) {
  #confirmationWrapper {
    display: grid;
    grid-template-rows: 0.5fr 4fr 6.5fr 6fr 0.8fr;
    grid-template-columns: 1fr 5fr 1fr;
  }

  #confirmationHeader {
    grid-column: 1 / span 3;
    grid-row: 1 / span 1;
  }

  #heroPanel {
    grid-column: 1 / span 3;
    grid-row: 2 / span 1;
  }

  #secondPanel {
    grid-column: 1 / span 3;
    grid-row: 3 / span 1;
  }

  #thirdPanel {
    grid-column: 2 / span 1;
    grid-row: 4 / span 1;
  }

  #confirmationFooter {
    grid-column: 1 /span 3;
    grid-row: 5 /span 1;
  }
}

#confirmationWrapper {
  background-color: #f6f6f8;
}

#confirmationHeader {
  background-image: linear-gradient(to right, #151f36, #5779da);
}

#confirmationHeader img {
  width: 150px;
  margin-top: 18px;
  margin-bottom: 18px;
  margin-left: 32px;
}

#heroPanel {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f6f6f8;
}

#confirmationTitle {
  font-family:  "Roboto Slab", sans-serif;
  font-size: 40px;
  font-weight: 300;
  color: #ff720b;
  margin-top: 45px;
}

#confirmationSub {
  font-family: sans-serif;
  font-size: 18px;
  font-weight: normal;
  color: #666666;
  margin-top: 32px;
}

#accountNumber {
  font-family: sans-serif;
  font-size: 18px;
  font-weight: normal;
  color: #666666;
  margin-top: 32px;
}

#userID {
  font-family: sans-serif;
  font-size: 18px;
  font-weight: normal;
  color: #666666;
}

#loginButton {
  border-radius: 5px;
  background-color: #ff720b;
  margin-top: 55px;
  height: 50px;
  width: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 60px;
}

#loginButton span {
  font-family: sans-serif;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
}

#secondPanel {
  background-color: #5879da;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#secondTitle {
  font-family:  "Roboto Slab", sans-serif;
  font-size: 28px;
  font-weight: 300;
  color: #fff;
  margin-top: 48px;
  margin-bottom: 48px;
  width: 40%;
}

#firstCardRow {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: space-between;
  width: 100%;
}

.confirmCard {
  display: flex;
  flex-direction: column;
  flex-basis: 292px;
  height: 166px;
  margin-right: 36px;
  background-color: #fff;
  justify-content: center;
  box-shadow: 0 8px 12px 3px rgba(0, 0, 0, 0.24);
}

.confirmCard:last-child{
  margin-right: 0;
  padding-right: 0px;
}

.cardImage {
  width: 50px;
}

@-moz-document url-prefix() {
    .cardImage {
        height: 80px;
    }
}

.cardContent {
  display: flex;
  margin-left: 24px;
  align-items: center;
  flex: 4;
}

.cardText {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-left: 17px;
  justify-content: center;
}

.cardTitle {
  font-family:  "Roboto Slab", sans-serif;
  font-size: 18px;
  font-weight: 300;
  color: #162036;
  margin-bottom: 10px;
}

.cardInnerText {
  font-family: sans-serif;
  font-size: 13px;
  color: #666666;
  padding-right: 15px;
}

.cardLinks {
  font-family: sans-serif;
  font-size: 13px;
  align-self: flex-start;
  margin-left: 24px;
  flex: 1;
}

.cardLinks a {
  color: #5879da;
  text-decoration: none;
}

.cardInnerText a {
  color: #5879da;
  text-decoration: none;
}

#secondCardRow {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: space-between;
  width: 100%;
  margin-bottom: 40px;
}

.secondConfirmCard {
  display: flex;
  flex-basis: 348px;
  height: 112px;
  margin-right: 30px;
  background-color: #fff;
  align-content: center;
  box-shadow: 0 8px 12px 3px rgba(0, 0, 0, 0.24);
}

.secondConfirmCard:last-child{
  margin-right: 0;
}

.secondConfirmCardImg {
  margin-left: 26px;
}

#thirdPanel {
  background-color: #f6f6f8;
  text-align: center;
  display: flex;
  flex-direction: column;
}

#thirdTitle {
  font-family:  "Roboto Slab", sans-serif;
  font-size: 28px;
  font-weight: 300;
  color: #162036;
  margin-top: 54px;
}

#thirdContentRow {
  display: flex;
}

.subjectRow {
  display: flex;
  text-align: left;
  margin-top: 50px;
}

.subjectColumn {
  font-family:  "Roboto Slab", sans-serif;
  font-size: 18px;
  color: #162036;
  flex: 1
}

.answerColumn {
  flex: 10;
}

.faqDivider {
  border: solid 2px #dddddd;
}

.answerRow {
  margin-left: 55px;
}

.answerRow:first-child {
  margin-bottom: 18px;
}

.answerTitle {
  font-family: sans-serif;
  font-size: 16px;
  font-weight: bold;
  color: #666666;
}

.answerContent {
  font-family: sans-serif;
  font-size: 13px;
  color: #666666;
}

#confirmationFooter {
  background-color: #e5e5e5;
  display: flex;
  align-items: center;
  justify-content: center;
}

#footerText {
  display: flex;
  font-family: sans-serif;
  font-size: 12px;
  color: #666666;
}

#footerText a {
  color: #5879da;
  text-decoration: none;
}

#footerText a::before {
  content: "\2022";
  margin-left: 5px;
  margin-right: 5px;
  color: #666666;
}

.inlineImage {
  margin: 0 5px;
}

/* Media queries begin, don't panic! */

@media screen and (max-width: 1240px) {

  .subjectColumn {
    flex: 2;
  }
}

@media screen and (max-width: 946px) {

  #confirmationWrapper {
    grid-template-rows: 0.5fr 3fr 7fr 6fr 0.5fr;
  }

  .confirmCard:nth-child(2) {
    margin-right: 0;
  }

  .confirmCard:last-child {
    margin-top: 36px;
    margin-left: 36px;
    margin-right: 36px;
  }
}

@media screen and (max-width: 724px) {

  .secondConfirmCard {
    margin-right: 0;
    margin-bottom: 30px;
  }

  .secondConfirmCard:last-child {
    margin-right: 30px;
    margin-left: 30px;
  }
}

@media screen and (max-width: 620px) {

  .confirmCard:first-child {
    margin-left: 36px;
  }

  .confirmCard {
    margin-bottom: 36px;
  }

  .confirmCard:last-child {
    margin-top: 0;
  }

  .subjectColumn {
    flex: 3;
  }
}

@media screen and (max-width:450px){

  @supports (display: grid) {

    #confirmationWrapper {
      grid-template-rows: 0.5fr 3fr 7fr 5fr 0.5fr;
    }
  }

  #confirmationHeader img {
    margin-top: 20px;
  }

  #confirmationTitle {
    width: 70%;
    font-size: 32px;
  }

  #confirmationSub {
    padding: 0 60px;
  }

  #loginButton {
    padding-right: 10px;
    padding-left: 10px;
  }

  #secondTitle {
    width: 80%;
  }

  .confirmCard {
    margin-right: 0;
    margin-bottom: 36px;
  }

  .confirmCard:first-child {
    margin-left: 0;
  }

  .secondConfirmCard {
    margin-right: 0;
    margin-bottom: 30px;
  }

  .secondConfirmCard:last-child {
    margin-right: 0;
    margin-left: 0;
  }

  .subjectRow {
    flex-direction: column;
    margin-left: 20px;
    margin-right: 20px;
  }

  .subjectColumn {
    margin-bottom: 10px;
  }

  .answerRow {
    margin-left: 0;
  }

  .answerRow:first-child {
    margin-top: 23px;
  }

  #footerText {
    flex-direction: column;
    text-align: center;
  }

  #footerText a:first-child::before {
    content: "";
    margin-left: 5px;
    margin-right: 5px;
    color: #666666;
  }

  .footerbottomspan {
    margin-bottom: 5px;
  }

}

@media screen and (max-width:360px){
  #confirmationWrapper {
    grid-template-rows: 0.5fr 2fr 6fr 5fr 0.5fr;
  }

  .confirmCard:last-child {
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
  }

  .secondConfirmCard {
    margin-right: 10px;
    margin-left: 10px;
  }

  .secondConfirmCard:last-child {
    margin-right: 10px;
    margin-left: 10px;
  }
}

.login-button-text {
  text-decoration: inherit;
  color: inherit;
  width: inherit;
  height: inherit;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.inner-button {
  width: 100%;
  height: 100%;
}
